.choose {
    margin: 0;
    position: relative;
    z-index: 1000; // highest value!!!
    width: 100%;
    height: 100vh;
    & > * {
        width: 50%;
        height: 100vh;
        transition: all 0.2s;
        overflow: hidden;
    }
    .left {
        background-image: linear-gradient(120deg, #f093fb 0%, #f5576c 100%);
        img {
            width: 15rem;
            position: relative;
        }
        .onleft{
            top: 30%;
            float: left;
            transform: translateY(-52%);
            margin-left: -7.5rem;
        }
        .onright{
            top: 65%;
            float: right;
            transform: translateY(-70%);
            margin-right: -7.5rem;
        }
        .text{
            width: auto;
            left: 50%;
            transform: translateX(-50%);
            margin-left: -7.5rem;
        }
    }
    .right {
        background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
        img {
            width: 15rem;
            position: relative;
            top: 50%; left: 50%;
            transform: translateX(-50%) translateY(50%);
            &:last-of-type{
                visibility: hidden;
                margin-left: -15rem;
                filter: blur(3px);
            }
        }
    }
    .text {
        width: 100%;
        font-family: 'Unica One', cursive;
        font-size: 1.8rem;
        color: $color-white;
        text-align: center;
        position: relative;
        z-index: 50;
        top: 50%;
        transition: all 0.4s;
    }
    cursor: pointer;
}